<template>
  <section class="components-block" :id="id">
    <h2 class="components-block__title">{{ capitalize(title) }}</h2>
    <p class="components-block__desc" v-html="desc"></p>
    <div class="components-block__example">
      <slot></slot>
    </div>
  </section>
</template>

<script>
export default {
  props: {
    id: {
      type: String,
      required: true,
    },
    title: {
      type: String,
      required: true,
    },
    desc: {
      type: String,
      default: "",
    },
    variants: {
      type: Array,
      default: () => [],
    },
  },
  methods: {
    capitalize(str) {
      return (str.charAt(0).toUpperCase() + str.slice(1)).split("-").join(" ");
    },
  },
};
</script>

